<script>
import { Category, Bar3d } from 'vo-ui'
import {bar3dData} from '../../components/data'
export default {
    name: 'checkbox',
    components: {
        Category, Bar3d
    },
    data() {
        return {
            title: '某个需求的柱状图',
            xAxisData: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"],
            seriesData: [{ // 2d 柱状图的 销量数据
                name: '销量',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }],
            bar3dData,  // 给3d 图的数据
        }
    }
}
</script>


